import uniRate from '../../components/uni-rate.vue'
export default {
	data() {
		return {
			id: '',
			name: '',
			url: '',
			asserssList: []
		}
	},
	components:{
		uniRate
	},
	onLoad(e) {
		console.log(e)
		this.name =e.name
		this.url = e.url
		this.id = e.id
		this.req()
	},
	methods: {
		req(){
			uni.request({
				url:this.nowUrl + '/foods/comment?id='+this.id,
				header:{
					'token':this.token
				},
				success:(res) => {
					this.asserssList = res.data.data.list
					console.log(this.asserssList)
				}
			})
		},
		toAssessSuccess(){
			uni.navigateTo({
				url:'../AssessSuccess/AssessSuccess',
				animationDuration:200,
				animationType:'slide-in-right'
			})
		}
	}
}
